import React from "react"
import {Sphere, MeshDistortMaterial} from "@react-three/drei"

export default function AnimatedSphere (){
    const random = Math.floor(Math.random()*10);
    let color1;
    if(random < 5 ){
        color1 = "cyan";
    }
    else{
        color1 = "purple";
    }
    return (
        < Sphere visible args={[1, 50, 100]} scale={1.4}>
           < MeshDistortMaterial 
           color={color1} 
            attach="material" 
            distort = {0.8}
            speed={5}
           />
        </Sphere>
      
    )
}